<template>
	<view class="_content" style="padding: 5rpx 20rpx 0rpx 20rpx;">
		<view class="topBox">
			<uni-segmented-control :current="current" size="200px" :values="items" :style-type="styleType"
				:active-color="activeColor" @clickItem="onClickItem" />
			<view style="padding: 10rpx; display: flex; align-items: center; background-color: #ffffff;">
				<view style="width: 25%;">筛选: 供应商</view>
				<view class="uni-col-view" @click="drawerOpen1"
					style="width: 50%; border: 1rpx solid #bdc3c7;padding-left: 9rpx;">{{ selectedItem1.cVenName
						|| '请点击选择' }} </view>
			</view>
		</view>
		<view class="_content" style="padding-top: 20rpx;">

			<view v-for="(item, index) in listData" :key="item.ID" class="card">
				<uni-row class="uni-row firstrow">
					<uni-col :span="18">
						<!-- 单个卡片复选框 -->
						<u-checkbox-group>
							<!-- 修改复选框的@change事件，传递item.ID -->
							<u-checkbox size="25px" :value="item.ID" :checked="isSelected(item.ID)"
								@change="toggleSelection(item.ID)"></u-checkbox>
						</u-checkbox-group>
						<!-- <view class="uni-col number">{{ index + 1 }}</view> -->
					</uni-col>
					<uni-col :span="6" class="divRight">
						<u-button type="primary" size="small" text="详情"
							@click="toDetails(listData[index].ID)"></u-button>
					</uni-col>
				</uni-row>
				<uni-row class="uni-row">
					<uni-col :span="12">
						<view class="uni-col textStyle">{{ item.cCode }}</view>
					</uni-col>
					<uni-col :span="12" class="textRight">
						<view class="uni-col">{{ item.dDate }}</view>
					</uni-col>
				</uni-row>
				<uni-row class="uni-row">
					<uni-col :span="12">
						<view>业务类型:</view>
					</uni-col>
					<uni-col :span="12" class="textRight">
						<view>{{ item.cBusType }}</view>
					</uni-col>
				</uni-row>
				<uni-row class="uni-row">
					<uni-col :span="12">
						<view>供应商:</view>
					</uni-col>
					<uni-col :span="12" class="textRight">
						<view>{{ item.cVenName }}</view>
					</uni-col>
				</uni-row>
			</view>
			<!-- 数据为空 -->
			<view v-if="this.listData.length == 0">
				<u-empty :mode="'data'" :icon="'https://cdn.uviewui.com/uview/demo/empty/data.png'">
				</u-empty>
			</view>
			<!-- 加载状态提示 -->
			<view class="load-more">
				<text v-if="loading">加载中...</text>
				<text v-else-if="noMoreData">没有更多数据了</text>
				<text v-else="this.listData.length > 10">上拉加载更多</text>
			</view>
		</view>
		<view style="height: 95rpx;" v-if="this.listData.length !== 0"></view>
		<view v-if="this.listData.length !== 0" class="sureBtn">
			<view class="select" style="width: 25%;">
				<u-checkbox-group v-if="this.listData || this.listData.length !== 0">
					<u-checkbox size="25px" label="全选" :checked="isAllSelected" @change="toggleAllSelection"
						customStyle="margin-bottom: 8px;">
					</u-checkbox>
				</u-checkbox-group>
			</view>
			<view style="width: 75%;">
				<u-button size="large" text="订单确认" color="#43C6AC" @click="orderComfirm"></u-button>
			</view>
		</view>
		<u-overlay :show="loding">
			<view class="warp">
				<view class="rect">
					<div class="dot-spinner">
						<div class="dot-spinner__dot"></div>
						<div class="dot-spinner__dot"></div>
						<div class="dot-spinner__dot"></div>
						<div class="dot-spinner__dot"></div>
						<div class="dot-spinner__dot"></div>
						<div class="dot-spinner__dot"></div>
						<div class="dot-spinner__dot"></div>
						<div class="dot-spinner__dot"></div>
					</div>
				</view>
			</view>
		</u-overlay>
		<!-- 供应商选择器 -->
		<u-popup :customStyle="select_popupStyle" overlay="false" :show="drawerRight1" closeable="true"
			@close="drawerClose1" mode="bottom" :round="10">
			<view class="_container">
				<!-- 搜索区域 -->
				<view class="search-header">
					<view class="search-box">
						<view style="width: 80%;">
							<u--input placeholder="请输入供应商编码或名称" border="surround" clearable v-model="searchInput1"
								suffixIcon="search" iconStyle="color: #909399;" class="search-input"
								@input="getVenList"></u--input>
						</view>
						<view style="width: 20%;">
							<u-button text="重置" @click="restSelectItem" class="action-btn"></u-button>
						</view>
					</view>
				</view>

				<!-- 查询结果显示区域 -->
				<view class="result-list" v-if="venList.length">
					<view v-for="(item, index) in venList" :key="index"
						:class="['result-item', { 'selected': selectedItem1.cVenCode === item.cVenCode }]"
						@click="selectItem1(item)">
						<text>{{ item.cVenCode }} - {{ item.cVenName }}</text>
					</view>
				</view>
				<view v-else class="no-data">
					<text>没有找到数据</text>
				</view>
			</view>
		</u-popup>
	</view>
</template>

<script>
import { orderMainList, orderConfirm, venList } from "@/nxTemp/apis/sunset.js"
export default {
	data() {
		return {
			select_popupStyle: {
				width: '100vw',
				// height: '100vh',
				backgroundColor: '#ffffff', // 添加背景色
				// borderRadius: '20rpx', // 添加圆角
			},

			currentPage: 1,
			pageSize: 20,
			loading: false,
			noMoreData: false,

			userInfo: [],
			items: ['采购订单', '委外订单', '工序加工单'],
			current: 0,
			colorIndex: 0,
			activeColor: '#43C6AC',
			styleType: 'button',

			loding: false,
			noMoreData: false,

			selectedIDs: [], // 存储选中的卡片ID
			drawer: false,//详情页显示状态

			type: '普通采购', //业务类型

			venList: [], //供应商列表
			searchInput1: '', //供应商查找关键字
			selectedItem1: null,  // 存储选中的供应商数据
			cVenCode: "", //01009  
			drawerRight1: false,	//查询供应商

			listData: [], //主数据列表
		};
	},
	onLoad() {
		uni.getStorage({
			key: 'userData',
			success: (res) => {
				this.userInfo = res.data; // 将用户数据赋值到当前页面的数据对象
			},
			fail: () => {
				uni.showToast({
					title: '用户数据读取失败，请重新登录',
					icon: 'none'
				});
				uni.reLaunch({
					url: '/pages/login/login'
				});
			}
		});
		// 延时2秒执行 onClickItem 方法
		setTimeout(() => {
			this.loding = true;
			this.getList();
			this.getVenList();
		}, 1000);
	},
	computed: {
		// 判断是否全选
		isAllSelected() {
			return this.selectedIDs.length === this.listData.length;
		}
	},

	methods: {
		async restSelectItem() {
			this.selectedItem1 = null;
			this.cVenCode = ''; // 清空 cInvCode
			this.listData = []
			await this.getList();
		},
		// 选中供应商项
		async selectItem1(item) {
			this.selectedItem1 = this.selectedItem1 === item ? null : item; // 切换选中状态
			if (this.selectedItem1 !== null) {
				this.cVenCode = item.cVenCode; // 更新 cInvCode
			} else {
				this.cVenCode = ''; // 清空 cInvCode
			}
			this.drawerRight1 = false;
			this.listData = []
			await this.getList();
		},
		drawerOpen1() {
			this.drawerRight1 = true;
		},
		drawerClose1() {
			this.drawerRight1 = false;
		},

		//进入详情
		toDetails(ID) {
			if (this.type == '工序委外') {
				//单独页面显示
				uni.navigateTo({ //参数为中文时会自动编码
					url: `/pages/confirm/gxww_details?id=${ID}&type=${this.type}`
				});
			} else {
				uni.navigateTo({ //参数为中文时会自动编码
					url: `/pages/confirm/detail?id=${ID}&type=${this.type}`
				});
			}

		},

		//单选方法
		toggleSelection(id) {
			const index = this.listData.findIndex(item => item.ID === id);
			const isSelected = this.selectedIDs.includes(id);

			if (isSelected) {
				// 取消选中时保留前面所有项（当前索引-1）
				this.selectedIDs = this.listData
					.slice(0, index)
					.map(item => item.ID);
			} else {
				// 选中时包含当前及之前所有项（当前索引+1）
				this.selectedIDs = this.listData
					.slice(0, index + 1)
					.map(item => item.ID);
			}
		},

		//全选方法
		toggleAllSelection() {
			if (this.isAllSelected) {
				this.selectedIDs = [];
			} else {
				this.selectedIDs = this.listData.map(item => item.ID);
			}
		},
		//是否选中
		isSelected(id) {
			return this.selectedIDs.includes(id);
		},

		//分页跳转
		async onClickItem(e) {
			this.listData = [];
			this.currentPage = 1;
			this.noMoreData = false;
			// this.selectedItem1 = null;
			// this.cVenCode = ''
			this.selectedIDs = [];//清空选项
			if (this.current !== e.currentIndex) {
				this.current = e.currentIndex;
			}
			if (e.currentIndex == 0) {
				this.type = '普通采购'
			} else if (e.currentIndex == 1) {
				this.type = '委外加工'
			} else if (e.currentIndex == 2) {
				this.type = '工序委外'
			}
			this.loding = true;
			await this.getList();
		},

		//查询主数据
		async getList() {
			this.loading = true;
			try {
				const res = await orderMainList({
					UserId: this.userInfo.UserId,
					OnlineCode: this.userInfo.OnlineCode,
					cBusType: this.type,
					cVenCode: this.cVenCode,
					page: this.currentPage,
					pageSize: this.pagesize,
				});

				if (res.success) {
					if (res.subdata.length > 0) {
						this.listData = this.listData.concat(res.subdata);
						this.currentPage++;
					} else {
						this.noMoreData = true;
					}
					//控制数据为空时的动画显示
					this.nodata = this.listData.length === 0;
				} else {
					uni.showToast({ title: res.message, icon: 'none' });
				}
			} catch (error) {
				uni.showToast({ title: '网络请求失败', icon: 'none' });
			} finally {
				this.loading = false;
				this.loding = false;
			}
		},


		//订单确认
		async orderComfirm() {
			console.log('订单确认项：', this.selectedIDs);
			if (this.loding || this.selectedIDs.length == 0) {
				if (this.selectedIDs.length == 0) {
					uni.showToast({ title: '请选择需要确认的订单', icon: 'none' });
				}
				return
			}
			this.loding = true;

			await orderConfirm({
				UserId: this.userInfo.UserId,
				OnlineCode: this.userInfo.OnlineCode,
				UserName: this.userInfo.UserName,   //登录用户名字
				cBusType: this.type,  //单据类型
				ListID: this.selectedIDs
			}).then(res => {
				if (res.success) {
					uni.showToast({ title: '确认成功' });
				} else {
					console.log(JSON.stringify(res));
					uni.showToast({ title: res.message, icon: 'none' });
				}
			}).catch(error => {
				uni.showToast({
					title: '异常:' + error.errMsg,
					icon: 'none'
				});
			})

			this.loding = false;
		},

		//供应商下拉树
		async getVenList(cVenCode) {
			let type = '';
			if (this.type == '普通采购') {
				type = '普通采购'
			} else if (this.type == '委外加工') {
				type = '委外加工'
			} else {
				type = '工序委外'
			}

			const res = await venList({
				UserId: this.userInfo.UserId,
				OnlineCode: this.userInfo.OnlineCode,
				cBusType: type,
				page: "1",
				pagesize: "100",
				cVenCode: cVenCode
			});

			if (res.success) {
				this.venList = res.subdata;
			} else {
				this.venList = []
			}
		}
	},// 上拉加载更多
	onReachBottom() {
		if (this.listData.length) {
			this.getList();
		}
	}
};
</script>


<style lang="less" scoped>
.load-more {
	text-align: center;
	padding: 20rpx;
	color: #999;
	font-size: 28rpx;
}

.uni-col-view {
	font-size: 26rpx;
	height: 60rpx;
	border-radius: 5px;
	display: flex;
	// justify-content: center;
	align-items: center;
}

//选择器样式
._container {
	margin-top: 10rpx;
	height: 65vh;
	display: flex;
	flex-direction: column;
	background: #f8f9fa;
	padding: 15rpx;
}

.search-header {
	width: 90%;
	padding: 10rpx 0;
	background: #fff;
	position: sticky;
	top: 20;
	z-index: 10;
}

.search-box {
	display: flex;
	align-items: center;
	gap: 10rpx;
	padding: 0 10rpx;
}

.search-input {
	flex: 1;
	font-size: 28rpx;
}

.action-btn {
	width: 120rpx;
	font-size: 24rpx;
}

.result-list {
	flex-grow: 1;
	overflow-y: auto;
	margin-top: 15rpx;
}

.result-row {
	display: flex;
	flex-wrap: wrap;
	/* 允许换行 */
	gap: 10rpx;
	/* 设定项目之间的间距 */
}

.result-item {
	flex: 0 0 32%;
	/* 每行显示三个，且每个项占 32% 的宽度 */
	padding: 10rpx;
	background: #fff;
	margin: 8rpx 0;
	border-radius: 8rpx;
	box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
	font-size: 40rpx;
	cursor: pointer;
	text-align: center;
}

.result-item.selected {
	background: #007BFF !important;
	/* 确保覆盖默认背景色 */
	color: #fff;
}

.no-data {
	text-align: center;
	margin-top: 15rpx;
	color: #aaa;
	font-size: 26rpx;
}

/* From Uiverse.io by akshat-patel28 */
.loader {
	position: relative;
	overflow: hidden;
	border-right: 3px solid;
	width: 0px;
	animation: typewriter 2s steps(10) infinite alternate, blink 0.5s steps(10) infinite;
}

.loader-text {
	font-size: 40px;
	font-weight: 700;
	background: linear-gradient(to right, #cb2d3e, #ef473a);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

@keyframes typewriter {
	0% {
		width: 0px;
	}

	100% {
		width: 240px;
	}
}

@keyframes blink {
	0% {
		border-right-color: rgba(255, 255, 255, .75);
		;
	}

	100% {
		border-right-color: transparent;
	}
}

.empty {
	width: 100%;
	min-height: 1100rpx;
	display: flex;
	/* 启用flex布局 */
	justify-content: center;
	/* 水平居中对齐 */
	align-items: center;
}

._content {
	// min-height: auto;
	font-size: medium;
	background-color: #f5f4f4;
}

.card {
	margin-bottom: 30rpx;
	padding: 5rpx 30rpx;
	border-radius: 0.5rem;
	// box-shadow: 0px 187px 75px rgba(0, 0, 0, 0.01), 0px 105px 63px rgba(0, 0, 0, 0.05), 0px 47px 47px rgba(0, 0, 0, 0.09), 0px 12px 26px rgba(0, 0, 0, 0.1), 0px 0px 0px rgba(0, 0, 0, 0.1);
	background-color: #FFFFFF;
}


.uni-row {
	// 组件在小程序端display为inline
	// QQ、抖音小程序文档写有 :host，但实测不生效
	// 百度小程序没有 :host
	/* #ifdef MP-TOUTIAO || MP-QQ || MP-BAIDU */
	display: block;
	/* #endif */
}

.firstrow {
	height: 70rpx;
	padding-top: 10rpx;
	border-bottom: 2rpx solid rgba(0, 0, 0, 0.3);
}

.uni-col {
	height: 50rpx;
	border-radius: 5px;
	display: flex;
	// justify-content: center;
	align-items: center;
}

.example-body {
	/* #ifndef APP-NVUE */
	display: block;
	/* #endif */
	padding: 5rpx 10rpx 0;
	overflow: hidden;
}

.textStyle {
	font-weight: 1000;
	color: #43C6AC;
}

.textRight {
	display: flex;
	justify-content: flex-end;
}

.divRight {
	text-align: right;
	display: flex;
	justify-content: flex-end;
}

.number {
	margin-left: 3rpx;
	font-size: medium;
	font-weight: 1000;
}

.select {
	padding-top: 10rpx;
	padding-left: 30rpx;
	display: flex;
	bottom: 0;
}

.sureBtn {
	width: 100%;
	height: 110rpx;
	padding: 20rpx 30rpx 20rpx 30rpx;
	/* 假设这是小程序中的单位 */
	position: fixed;
	/* 使按钮固定在视口 */
	display: flex;
	justify-content: center;
	align-items: center;
	left: 50%;
	/* 将按钮的左边缘定位到屏幕宽度的 50% */
	transform: translateX(-50%);
	/* 水平居中 */
	bottom: 0;
	/* 距离屏幕底部 20px */
	z-index: 200;
	/* 确保它位于其他内容之上 */
	background-color: #fdf7f7;
	// background-color: rgba(0, 0, 0, 0.0); /* 半透明背景 */
	/* 按钮背景颜色 */
	font-size: 20px;
	font-weight: bold;
	font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
	color: #FFFFFF;
	/* 按钮文字颜色 */
	border-radius: 0.5rem;
	/* 圆角边框 */
}

.button-hover {
	background: #11998e;
}

.warp {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100%;
}

.rect {
	width: 120px;
	height: 120px;
	display: flex;
	/* 启用flex布局 */
	justify-content: center;
	/* 水平居中对齐 */
	align-items: center;
	// background-color: #fff;
}


/* From Uiverse.io by abrahamcalsin */
.dot-spinner {
	--uib-size: 2.8rem;
	--uib-speed: .9s;
	--uib-color: #43C6AC;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	height: var(--uib-size);
	width: var(--uib-size);
}

.dot-spinner__dot {
	position: absolute;
	top: 0;
	left: 0;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	height: 100%;
	width: 100%;
}

.dot-spinner__dot::before {
	content: '';
	height: 20%;
	width: 20%;
	border-radius: 50%;
	background-color: var(--uib-color);
	transform: scale(0);
	opacity: 0.5;
	animation: pulse0112 calc(var(--uib-speed) * 1.111) ease-in-out infinite;
	box-shadow: 0 0 20px rgba(18, 31, 53, 0.3);
}

.dot-spinner__dot:nth-child(2) {
	transform: rotate(45deg);
}

.dot-spinner__dot:nth-child(2)::before {
	animation-delay: calc(var(--uib-speed) * -0.875);
}

.dot-spinner__dot:nth-child(3) {
	transform: rotate(90deg);
}

.dot-spinner__dot:nth-child(3)::before {
	animation-delay: calc(var(--uib-speed) * -0.75);
}

.dot-spinner__dot:nth-child(4) {
	transform: rotate(135deg);
}

.dot-spinner__dot:nth-child(4)::before {
	animation-delay: calc(var(--uib-speed) * -0.625);
}

.dot-spinner__dot:nth-child(5) {
	transform: rotate(180deg);
}

.dot-spinner__dot:nth-child(5)::before {
	animation-delay: calc(var(--uib-speed) * -0.5);
}

.dot-spinner__dot:nth-child(6) {
	transform: rotate(225deg);
}

.dot-spinner__dot:nth-child(6)::before {
	animation-delay: calc(var(--uib-speed) * -0.375);
}

.dot-spinner__dot:nth-child(7) {
	transform: rotate(270deg);
}

.dot-spinner__dot:nth-child(7)::before {
	animation-delay: calc(var(--uib-speed) * -0.25);
}

.dot-spinner__dot:nth-child(8) {
	transform: rotate(315deg);
}

.dot-spinner__dot:nth-child(8)::before {
	animation-delay: calc(var(--uib-speed) * -0.125);
}

@keyframes pulse0112 {

	0%,
	100% {
		transform: scale(0);
		opacity: 0.5;
	}

	50% {
		transform: scale(1);
		opacity: 1;
	}
}
</style>
